
import { defineComponent, ref } from 'vue'

export default defineComponent({
  props: {
    filterStr: {
      type: String,
      default: ''
    }
  },
  emits: ['clear', 'submit'],
  setup (props, { emit }) {
    let visible = ref(false)

    const reback = (): void => {
      visible.value = false
    }
    const clear = (): void => {
      emit('clear')
    }
    const submit = ():void => {
      visible.value = false
      emit('submit')
    }

    return {
      visible,
      reback,
      clear,
      submit
    }
  }
})

